

// Variables
@import "variables";

.circle-shopping-cart{
	background-color: #232980;
	border-radius: 50%;
	padding: 1em 1em;
}

.products-container{
	.product{
		margin-top: 1em;
	}
}

.product-avatar{
	max-width: 100%;
}
.banner{

}

.navbar{
	margin-bottom:0px;
}

.no-margin{
	margin:0px;
}
.top-space{
	margin-top: 1em;
}

.no-padding{
	padding: 0px;
}

.large-padding{
	padding: 10px 15px;
}

.big-padding{
	padding:30px;
}

.no-transform{
	text-transform:none;
}

.white{
	background-color:white;
}

.blue-grey{
	background-color:#37474f;
}

.white-text{
	color:white;
}

.inline-block{
	
	display:inline-block;
}

.table{
	background-color:white;
}

.red-text{
	color:rgb(200,5,50);
}


.table{
	background-color: white;
}

.absolute{
	position: absolute;
}

.floating{
	position: fixed;
	bottom: 2em;
	right: 2em;
	z-index: 100;
}

.product{
	width:600px;
	max-width: 100%;
	padding:1em;
	position: relative;
	h1{
		padding-bottom: 10px;
		border-bottom: solid 1px;
	}
	.actions{
		right: 1em;
		top: 10px;
	}

}

.sale-data{
	padding: 1em;
	text-align: center;
	border-right: solid 1px #222;
	span{
		display: block;
		font-size: 2.2em;
		color: #3f51b5;
	}

}

//status order

.creado{
	background-color: #ffffffff;
}

.highlight{
	-webkit-animation: 1.5s;
	animation: highlight 1.5s;
}

@-webkit-keyframes highlight {
	50% {
		background-color: #ffff00;
		color:black;
	}
	100% {
		background-color: inherit;
	}

}

@keyframes higlight {
	50% {
		background-color: #ffff00;
		color: black;
	}
	100% {
		background-color: inherit;
	}

}